import useInput from '@hooks/useInput';
import { Form, Error, Label, Input, LinkContainer, Button, Header } from '@pages/SignUp/styles';
import fetcher from '@utils/fetcher';
import axios from 'axios';
import React, { useCallback, useState } from 'react';
import { Link, Navigate } from 'react-router-dom';
import useSWR from 'swr';

const LogIn = () => {
    const { data, error, mutate } = useSWR('/api/users', fetcher, {
        dedupingInterval: 2000 //2
    });

    const [logInError, setLogInError] = useState(false);
    const [email, onChangeEmail] = useInput('');
    const [password, onChangePassword] = useInput('');

    const onSubmit = useCallback(
        (e) => {
            e.preventDefault();
            setLogInError(false);
            axios
                .post(
                    '/api/users/login',
                    { email, password },
                    {
                        withCredentials: true,
                    },
                )
                .then((response) => {
                    mutate(response.data, false) //OPTIMISTIC UI :       
                })
                .catch((error) => {
                    setLogInError(error.response?.data?.statusCode === 401);
                });
        },
        [email, password],
    );

    if (data === undefined) {
        return <div>...</div>;
    }

    if (data) {
        return <Navigate to="/workspace" />;
    }

    // console.log(error, userData);
    // if (!error && userData) {
    //   console.log('', userData);
    //   return <Redirect to="/workspace/sleact/channel/" />;
    // }

    return (
        <div id="container__login">
            <Header>HelloReview</Header>
            <Form onSubmit={onSubmit}>
                <Label id="email-label">
                    <span> </span>
                    <div>
                        <Input type="email" id="email" name="email" value={email} onChange={onChangeEmail} />
                    </div>
                </Label>
                <Label id="password-label">
                    <span></span>
                    <div>
                        <Input type="password" id="password" name="password" value={password} onChange={onChangePassword} />
                    </div>
                    {logInError && <Error>    .</Error>}
                </Label>
                <Button type="submit"></Button>
            </Form>
            <LinkContainer>
                  ?&nbsp;
                <Link to="/signup"> </Link>
            </LinkContainer>
        </div>
    );
};

export default LogIn;
